<template>
  <div class="hello flex">
    <ul class="flex-1">
      <li
        v-for="(item, i) in list"
        :key="i"
        :id="`li-${i}`"
        @click="copy(item)">
        {{ item }}
      </li>
    </ul>
    <div class="btn-wrap">
      <p><a style="background: #42b983;color: antiquewhite">注 册 号</a> <input v-model="engrno" style="color: #42b983" type="text" name="" id=""/></p>
      <p><button @click="start">开始查询</button></p>
      <p><button @click="stop">结束查询</button></p>
    </div>
    <input type="text" v-model="selectText" id="inp" style="display: none;" />
  </div>

</template>

<script>
  import axios from 'axios';

  var toekn =localStorage.getItem("check_info");
  export default {
    name: 'HelloWorld',
    data() {
      return {
        path: "ws://localhost:8080/ws/"+toekn,
        socket: "",
        list: [],
        selectText: '',
      }
    },
    mounted() {
      // 初始化
      this.init()
    },
    methods: {
      copy(str) {
       this.selectText = str;
       this.$nextTick(() => {
         const dom = document.getElementById('inp');
        if (dom) {
          dom.select();
          document.execCommand("Copy");

        }
       });

      },
      start(){
        axios.post('http://localhost:8080/startQuer', {
          ENTREGNO: this.engrno        // 参数 firstName
        })
          .then((response) => {
          if(response.data.code === 0){
            localStorage.setItem("taskId",response.data.data)
        }else {alert(response.data.msg)}
      })
      .catch(function (error) {
          console.log(error);
        });
      },
      stop(){
        axios.post('http://localhost:8080/stopQuer', {
          ENTREGNO: localStorage.getItem("taskId")        // 参数 firstName
        })
          .then((response) => {
          if(response.data.code === 0){
         alert("stop_success")
        }else {alert(response.data.msg)}
      })
      .catch(function (error) {
          console.log(error);
        });
      },
      init: function () {
        if (typeof (WebSocket) === "undefined") {
          alert("您的浏览器不支持socket")
        } else {
          // 实例化socket
          this.socket = new WebSocket(this.path)
          // 监听socket连接
          this.socket.onopen = this.open
          // 监听socket错误信息
          this.socket.onerror = this.error
          // 监听socket消息
          this.socket.onmessage = this.getMessage
        }
      },
      open: function () {
        console.log("socket连接成功")
      },
      error: function () {
        console.log("连接错误")
      },
      getMessage: function (msg) {
        console.log(msg.data)
        this.list.push(msg.data);
      },
      send: function () {
        this.socket.send(params)
      },
      close: function () {
        console.log("socket已经关闭")
      }
    },
    destroyed() {
      // 销毁监听
      this.socket.onclose = this.close
    }
  }


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .flex {
    display: flex;
  }

  .flex-1 {
    flex: 1;
  }

  .btn-wrap {
    width: 500px;
  }

  .hello {
    padding: 10px;
    margin: 0 auto;
  }
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    background: #42b983;
    margin-bottom: 10px;

  }

  a {
    color: #42b983;
  }
</style>



